<template>
    <div>
        <headTop signin-up="msite">
            <div slot="search" @click="goSearch('/search/'+geohash)" class="link_search">
                <svg width="100%" height="100%" xmlns="http://www.w3.org/2000/svg" verson="1.1">
                    <circle r="8" cx="9" cy="9" fill="none" stroke="#fff" stroke-width="2"/>
                    <line x1="15" y1="15" x2="20" y2="20" stroke="#fff" stroke-width="2"/>
                </svg>
            </div>
            <div class="msite_title" @click="goHome('/home')" slot="msite-title">
                <span class="title_text ellipsis">{{msiteTitle}}</span>
            </div>
        </headTop>
        <nav class="msite_nav">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide food_types_container" v-for="(item, index) in foodTypes" :key="index">
                        <div class="link_to_food" v-for="foodItem in item" :key="foodItem.id" v-if="foodItem.title!='预定早餐'" @click="goFood({path: '/food',query:{geohash,title: foodItem.title,restaurant_category_id: getCategoryId(foodItem.link)}})">
                            <figure>
                                <img :src="imgBaseUrl + foodItem.image_url">
                                <figcaption>{{foodItem.title}}</figcaption>
                            </figure> 
                        </div>
                        <a v-else href="https://zaocan.ele.me" class="link_to_food">
                            <figure>
                                <img :src="imgBaseUrl + foodItem.image_url">
                                <figcaption>{{foodItem.title}}</figcaption>
                            </figure>
                        </a>
                    </div>
                </div>
                <div class="swiper-pagination"></div>
            </div>
        </nav>
        <div class="shop_list_container">
            <header class="shop_header">
                <svg class="shop_icon">
                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#shop"></use>
                </svg>
                <span class="shop_header_title">附近商家</span>
            </header>
            <shopList v-if="hasGetData" :geohash="geohash"></shopList>
        </div>
        <bottom></bottom>
    </div>
</template>
<script type="text/javascript">
    import {mapMutations} from 'vuex'
    import {imgBaseUrl} from '../../../config/env'
    import headTop from '../header/header'
    import shopList from '../common/shoplist'
    import bottom from '../footer/footGuide'
    import {msiteAdress, msiteFoodTypes, msiteShopList} from '../../../service/getData'
    import '../../../style/swiper.min.css'
    import '../../../plugin/swiper.min.js'
    export default {
        data(){
            return {
                geohash: '',
                msiteTitle: '请选择地址...',
                foodTypes: [],
                hasGetData: false,
                imgBaseUrl
            }
        },
        async beforeMount(){
            this.geohash = this.$route.query.geohash || 'wtw3sm0q087'
            this.SAVE_GEOHASH(this.geohash)
            let res = await msiteAdress(this.geohash)
            this.msiteTitle = res.name
            this.RECORD_ADDRESS(res)
            this.hasGetData = true
        },
        mounted(){
            msiteFoodTypes(this.geohash).then(res => {
                let resLength = res.length,
                    resArr = res.concat([]),
                    foodArr = [];
                for(let i=0,j=0;i<resLength;i+=8,j++){
                    foodArr[j] = resArr.splice(0,8)
                }
                this.foodTypes = foodArr
            }).then(() => {
                new Swiper('.swiper-container', {
                    pagination: '.swiper-pagination',
                    loop: true
                });
            })
        },
        components: {
            headTop,
            shopList,
            bottom
        },
        methods: {
            ...mapMutations([
                'RECORD_ADDRESS', 'SAVE_GEOHASH'
            ]),
            getCategoryId(url){
                let urlData = decodeURIComponent(url.split('=')[1].replace('&target_name',''))
                if(/restaurant_category_id/gi.test(urlData)){
                    return JSON.parse(urlData).restaurant_category_id
                }else{
                    return ''
                }
            },
            goSearch(path){
                this.$router.push(path)
            },
            goHome(path){
                this.$router.push(path)
            },
            goFood(path){
                this.$router.push(path)
            }
        }
    }
</script>
<style lang="scss" scoped>
    @import '../../../style/mixin';
    .link_search{
        left: .8rem;
        @include wh(.8rem, .9rem);
        @include ct;
    }
    .msite_title{
        @include center;
        width: 50%;
        color: #fff;
        text-align: center;
        margin-left: -0.5rem;
        .title_text{
            @include sc(0.8rem, #fff);
            text-align: center;
            display: block;
        }
    }
    .msite_nav{
        padding-top: 2.1rem;
        background-color: #fff;
        border-bottom: 0.025rem solid $bc;
        .swiper-container{
            @include wh(100%, auto);
            padding-bottom: 0.6rem;
            .swiper-pagination{
                bottom: 0.2rem;
            }
        }
    }
    .food_types_container{
        display:flex;
        flex-wrap: wrap;
        .link_to_food{
            width: 25%;
            padding: 0.3rem 0rem;
            @include fj(center);
            figure{
                img{
                    margin-bottom: 0.3rem;
                    @include wh(1.8rem, 1.8rem);
                }
                figcaption{
                    text-align: center;
                    @include sc(0.55rem, #666);
                }
            }
        }
    }
    .shop_list_container{
        margin-top: .4rem;
        border-top: 0.025rem solid $bc;
        background-color: #fff;
        .shop_header{
            .shop_icon{
                fill: #999;
                margin-left: 0.6rem;
                vertical-align: middle;
                @include wh(0.6rem, 0.6rem);
            }
            .shop_header_title{
                color: #999;
                @include font(0.55rem, 1.6rem);
            }
        }
    }

</style>



